.nav {
  display: block;
  
  &__list {
    display: flex;
    padding: 0;
    margin: 0;
  
    list-style: none;
  }
  
  &__item:not(:first-child) {
    margin-left: 20px;
  }
  
  &__link {
    position: relative;
    display: flex;
    align-items: center;
    height: 60px;
    white-space: nowrap;
  
    font-family: "Roboto", sans-serif;
    font-size: 12px;
    font-weight: 500;
  
    text-decoration: none;
    text-transform: uppercase;
    color: #060b35;

    &:hover {
      color: #00acdc;
    }
  }
}
